import Color from '../color';

import calculateLensPosition from './utils/calculateLensPosition';

export const updatePicker = ({
	state: {
		updateLensOnPropChange,
		properties: {
			selectedColorHsv: {h, s, v}
		}
	},
	updateState
}) => {
	if (updateLensOnPropChange) {
		const lens = calculateLensPosition(v, s);

		// Increase saturation of color to ensure that it displays well in the picker
		const baseColor = new Color();
		baseColor.setHSV({h, s: 1, v: 1});

		updateState({baseColor: baseColor.getColor(), lens});
	} else {
		updateState({updateLensOnPropChange: true});
	}
};
