import changeHex from './utils/changeHex';
import changeRgba from './utils/changeRgba';

export const handleInitialProperties = ({
	updateState,
	state: {
		properties: {selectedColorRgba}
	}
}) => {
	const fieldValuesRgba = {...selectedColorRgba};
	// Rounding is needed because of JS not handling multiplication with a float gracefully
	fieldValuesRgba.a = Math.round(fieldValuesRgba.a * 100);

	updateState({
		path: 'fieldValues.rgba',
		value: fieldValuesRgba,
		operation: 'merge'
	});
};

export const colorChange = (
	{
		state: {
			properties: {selectedColorRgba}
		},
		action: {
			payload: {fieldValue: value, name}
		},
		dispatch,
		updateState
	}
) => {
	if (name === 'hex') {
		updateState({
			path: 'fieldValues.hex',
			value,
			operation: 'merge'
		});

		changeHex(value, dispatch);
	} else {
		updateState({
			path: `fieldValues.rgba.${name}`,
			value,
			operation: 'merge'
		});

		changeRgba({value, selectedColorRgba, key: name, dispatch});
	}
};

export const changeSelectedFieldsType = (
	{
		action: {
			payload: {value}
		},
		updateState
	}
) => updateState({selectedFieldsType: value[0]});
