import {t} from 'sn-translate';

import '@servicenow/now-label-value';
import '@servicenow/now-dropdown';
import '@servicenow/now-input';

import {RGBA, HEX} from './constants';
import validateRgbaInput from './utils/validateRgbaInput';
import validateHexInput from './utils/validateHexInput';

const fieldTypesItems = [
	{id: HEX, label: t('HEX')},
	{id: RGBA, label: t('RGBA')}
];

const renderRGBAFields = selectedColorRgba => {
	const rgbaFields = [
		{
			label: t('R'),
			key: 'r',
			min: 0,
			max: 255
		},
		{
			label: t('G'),
			key: 'g',
			min: 0,
			max: 255
		},
		{
			label: t('B'),
			key: 'b',
			min: 0,
			max: 255
		},
		{
			label: t('A'),
			key: 'a',
			min: 0,
			max: 100
		}
	];

	return <fieldset className="color-picker-color-fields">
		<div className="color-picker-color-fields__rgba-container">
			<legend className="a11y">RGBA format</legend>
			{rgbaFields &&
				rgbaFields.map(({label, key, min, max}) => {
					const value = selectedColorRgba[key];

					return <div className="color-picker-color-fields__input color-picker-color-fields__input--rgba">
						<now-input
							label={label}
							type="text"
							value={value}
							name={key}
							helperContent={t('This field accepts values from {0} to {1}', min, max)}
							on-keydown={event => validateRgbaInput({event, key, min, max})}
						/>
					</div>;
				})
			}
		</div>
	</fieldset>;
};

const renderHEXField = selectedColorHex => <div className="color-picker-color-fields__input">
	<now-input
		label={t('HEX')}
		value={selectedColorHex}
		name="hex"
		on-keydown={event => validateHexInput(event, 'hex')}
	/>
</div>;

export default ({
	selectedFieldsType,
	fieldValues: {rgba},
	properties: {selectedColorHex}
}) => (
	<div>
		<now-label-value-inline
			label={t('Color Format')}
			value=""
			configAria={{
				'aria-hidden': true
			}}
			className="color-picker-color-fields__dropdown__label"
		/>
		<now-dropdown
			items={fieldTypesItems}
			selectedItems={[selectedFieldsType]}
			size="sm"
			variant="secondary"
			select="single"
			bare={true}
			configAria={{
				'aria-label': t('Color Format: {0}', selectedFieldsType)
			}}
			className="color-picker-color-fields__dropdown__select"
		/>
		{selectedFieldsType === RGBA ? renderRGBAFields(rgba) : ''}
		{selectedFieldsType === HEX ? renderHEXField(selectedColorHex) : ''}
	</div>
);
