import {INTERNAL_COLOR_SET} from '../../constants';

import getHueFamily from './getHueFamily';
import getA11yMessage from './getA11yMessage';

export default ({
	newValue,
	selectedColorHsv,
	selectedColorRgba,
	valueKey,
	dispatch,
	updateState
}) => {
	let value = parseInt(newValue);
	let selectedColor = selectedColorHsv;

	const isAlpha = valueKey === 'a';
	const hueFamily = isAlpha ? null : getHueFamily(value);

	const a11yMessage = getA11yMessage({
		isHue: !isAlpha,
		hueFamily,
		value: Math.round(value)
	});

	if (isAlpha) {
		value = value / 100;
		selectedColor = selectedColorRgba;
	}

	updateState({
		a11yMessage,
		hueFamily
	});

	dispatch(INTERNAL_COLOR_SET, {
		value: Object.assign({}, selectedColor, {[valueKey]: value})
	});
};
