/**
 * Represents a colour. Deals with colourspace translations etc.
 * @constructor
 */
function Color() {
	// Default to a blue
	this.r = 10;
	this.g = 10;
	this.b = 252;
	this.a = 1;
}

Color.prototype = (function() {
	// Forward lookup of color names
	const W3C_COLOR = {
		aliceblue: '#f0f8ff',
		antiquewhite: '#faebd7',
		aqua: '#00ffff',
		aquamarine: '#7fffd4',
		azure: '#f0ffff',
		beige: '#f5f5dc',
		bisque: '#ffe4c4',
		black: '#000000',
		blanchedalmond: '#ffebcd',
		blue: '#0000ff',
		blueviolet: '#8a2be2',
		brown: '#a52a2a',
		burlywood: '#deb887',
		cadetblue: '#5f9ea0',
		chartreuse: '#7fff00',
		chocolate: '#d2691e',
		coral: '#ff7f50',
		cornflowerblue: '#6495ed',
		cornsilk: '#fff8dc',
		crimson: '#dc143c',
		cyan: '#00ffff',
		darkblue: '#00008b',
		darkcyan: '#008b8b',
		darkgoldenrod: '#b8860b',
		darkgray: '#a9a9a9',
		darkgreen: '#006400',
		darkkhaki: '#bdb76b',
		darkmagenta: '#8b008b',
		darkolivegreen: '#556b2f',
		darkorange: '#ff8c00',
		darkorchid: '#9932cc',
		darkred: '#8b0000',
		darksalmon: '#e9967a',
		darkseagreen: '#8fbc8f',
		darkslateblue: '#483d8b',
		darkslategray: '#2f4f4f',
		darkturquoise: '#00ced1',
		darkviolet: '#9400d3',
		deeppink: '#ff1493',
		deepskyblue: '#00bfff',
		dimgray: '#696969',
		dodgerblue: '#1e90ff',
		firebrick: '#b22222',
		floralwhite: '#fffaf0',
		forestgreen: '#228b22',
		fuchsia: '#ff00ff',
		gainsboro: '#dcdcdc',
		ghostwhite: '#f8f8ff',
		gold: '#ffd700',
		goldenrod: '#daa520',
		gray: '#808080',
		green: '#008000',
		greenyellow: '#adff2f',
		honeydew: '#f0fff0',
		hotpink: '#ff69b4',
		indianred: '#cd5c5c',
		indigo: '#4b0082',
		ivory: '#fffff0',
		khaki: '#f0e68c',
		lavender: '#e6e6fa',
		lavenderblush: '#fff0f5',
		lawngreen: '#7cfc00',
		lemonchiffon: '#fffacd',
		lightblue: '#add8e6',
		lightcoral: '#f08080',
		lightcyan: '#e0ffff',
		lightgoldenrodyellow: '#fafad2',
		lightgrey: '#d3d3d3',
		lightgreen: '#90ee90',
		lightpink: '#ffb6c1',
		lightsalmon: '#ffa07a',
		lightseagreen: '#20b2aa',
		lightskyblue: '#87cefa',
		lightslategray: '#778899',
		lightsteelblue: '#b0c4de',
		lightyellow: '#ffffe0',
		lime: '#00ff00',
		limegreen: '#32cd32',
		linen: '#faf0e6',
		magenta: '#ff00ff',
		maroon: '#800000',
		mediumaquamarine: '#66cdaa',
		mediumblue: '#0000cd',
		mediumorchid: '#ba55d3',
		mediumpurple: '#9370d8',
		mediumseagreen: '#3cb371',
		mediumslateblue: '#7b68ee',
		mediumspringgreen: '#00fa9a',
		mediumturquoise: '#48d1cc',
		mediumvioletred: '#c71585',
		midnightblue: '#191970',
		mintcream: '#f5fffa',
		mistyrose: '#ffe4e1',
		moccasin: '#ffe4b5',
		navajowhite: '#ffdead',
		navy: '#000080',
		oldlace: '#fdf5e6',
		olive: '#808000',
		olivedrab: '#6b8e23',
		orange: '#ffa500',
		orangered: '#ff4500',
		orchid: '#da70d6',
		palegoldenrod: '#eee8aa',
		palegreen: '#98fb98',
		paleturquoise: '#afeeee',
		palevioletred: '#d87093',
		papayawhip: '#ffefd5',
		peachpuff: '#ffdab9',
		peru: '#cd853f',
		pink: '#ffc0cb',
		plum: '#dda0dd',
		powderblue: '#b0e0e6',
		purple: '#800080',
		red: '#ff0000',
		rosybrown: '#bc8f8f',
		royalblue: '#4169e1',
		saddlebrown: '#8b4513',
		salmon: '#fa8072',
		sandybrown: '#f4a460',
		seagreen: '#2e8b57',
		seashell: '#fff5ee',
		sienna: '#a0522d',
		silver: '#c0c0c0',
		skyblue: '#87ceeb',
		slateblue: '#6a5acd',
		slategray: '#708090',
		snow: '#fffafa',
		springgreen: '#00ff7f',
		steelblue: '#4682b4',
		tan: '#d2b48c',
		teal: '#008080',
		thistle: '#d8bfd8',
		tomato: '#ff6347',
		turquoise: '#40e0d0',
		violet: '#ee82ee',
		wheat: '#f5deb3',
		white: '#ffffff',
		whitesmoke: '#f5f5f5',
		yellow: '#ffff00',
		yellowgreen: '#9acd32'
	};

	// Reverse lookup of colour names
	const REV_W3C_COLOR = {};
	for (const key in W3C_COLOR) REV_W3C_COLOR[W3C_COLOR[key]] = key;

	/**
	 * Pads single character hex color to double character e.g. F -> FF
	 * @param {string} colorHex
	 */
	function padColorHex(colorHex) {
		return colorHex.length < 2 ? colorHex + colorHex : colorHex;
	}

	//Conversions for valid W3C CSS colour types
	const CONVERT = {
		RGB_3: {
			p: /^\s*#([0-9a-f]{1})([0-9a-f]{1})([0-9a-f]{1})\s*$/i,
			f(match) {
				return {
					r: parseInt(padColorHex(match[1]), 16),
					g: parseInt(padColorHex(match[2]), 16),
					b: parseInt(padColorHex(match[3]), 16),
					a: 1
				};
			}
		},
		RGB_6: {
			p: /^\s*#([0-9a-f]{2})([0-9a-f]{2})([0-9a-f]{2})\s*$/i,
			f(match) {
				return {
					r: parseInt(match[1], 16),
					g: parseInt(match[2], 16),
					b: parseInt(match[3], 16),
					a: 1
				};
			}
		},
		RGBA_4: {
			p: /^\s*#([0-9a-f]{1})([0-9a-f]{1})([0-9a-f]{1})([0-9a-f]{1})\s*$/i,
			f(match) {
				return {
					r: parseInt(padColorHex(match[1]), 16),
					g: parseInt(padColorHex(match[2]), 16),
					b: parseInt(padColorHex(match[3]), 16),
					a: enforceNormalizedRange(parseInt(padColorHex(match[4]), 16) / 255)
				};
			}
		},
		RGBA_8: {
			p: /^\s*#([0-9a-f]{2})([0-9a-f]{2})([0-9a-f]{2})([0-9a-f]{2})\s*$/i,
			f(match) {
				return {
					r: parseInt(match[1], 16),
					g: parseInt(match[2], 16),
					b: parseInt(match[3], 16),
					a: enforceNormalizedRange(parseInt(match[4], 16) / 255)
				};
			}
		},
		RGB_CSS: {
			p: /^\s*rgb\s*\(\s*(\d{1,3})\s*,\s*(\d{1,3})\s*,\s*(\d{1,3})\s*\)\s*$/,
			f(match) {
				return {
					r: enforce8BitRange(match[1]),
					g: enforce8BitRange(match[2]),
					b: enforce8BitRange(match[3]),
					a: 1
				};
			}
		},
		RGBA_CSS: {
			p: /^\s*rgba\s*\(\s*(\d{1,3})\s*,\s*(\d{1,3})\s*,\s*(\d{1,3})\s*,\s*(1(\.0)?|0(.\d+)?)\s*\)\s*$/,
			f(match) {
				return {
					r: enforce8BitRange(match[1]),
					g: enforce8BitRange(match[2]),
					b: enforce8BitRange(match[3]),
					a: enforceNormalizedRange(match[4])
				};
			}
		},
		HSL_CSS: {
			p: /^\s*hsl\s*\(\s*(\d{1,3})\s*,\s*(\d{1,3})%\s*,\s*(\d{1,3})%\s*\)\s*$/,
			f(match) {
				return hsl2rgb({
					h: enforceDegreeRange(match[1]),
					s: enforceNormalizedRange(match[2] / 100),
					l: enforceNormalizedRange(match[3] / 100),
					a: 1
				});
			}
		},
		HSLA_CSS: {
			p: /^\s*hsla\s*\(\s*(\d{1,3})\s*,\s*(\d{1,3})%\s*,\s*(\d{1,3})%\s*,\s*(1(\.0)?|0(.\d+)?)\s*\)\s*$/,
			f(match) {
				return hsl2rgb({
					h: enforceDegreeRange(match[1]),
					s: enforceNormalizedRange(match[2] / 100),
					v: enforceNormalizedRange(match[3] / 100),
					a: enforceNormalizedRange(match[4])
				});
			}
		},
		evalOrder: [
			'RGB_6',
			'RGB_3',
			'RGBA_CSS',
			'RGB_CSS',
			'RGBA_4',
			'RGBA_8',
			'HSL_CSS',
			'HSLA_CSS'
		]
	};

	/**
	 * Enforces an 8 Bit value range, 0-255
	 * @param {number} val
	 */
	function enforce8BitRange(val) {
		return Math.max(0, Math.min(255, Math.round(val)));
	}

	/**
	 * Enforces hue degrees range, 0-360
	 * @param {number} val
	 */
	function enforceDegreeRange(val) {
		return Math.max(0, Math.min(360, val));
	}

	/**
	 * Enforces an normalised range, 0-1
	 * @param {number} val
	 */
	function enforceNormalizedRange(val) {
		return Math.max(0, Math.min(1, val));
	}

	/**
	 * Converts a normalised 0-1 based value to an 8 bit value 0-255
	 * @param {number} val
	 */
	function normalizedTo8Bit(val) {
		return enforce8BitRange(val * 255);
	}

	/**
	 * Checks is value undefined or null
	 * @param {number} val
	 */
	function isUndefinedOrNull(val) {
		return val === undefined || val === null;
	}

	function hsl2rgb(hsl) {
		function toRGBVal(t1, t2, tCol) {
			let validTCol = tCol;
			if (validTCol < 0)
				validTCol += 1;
			if (validTCol > 1)
				validTCol -= 1;

			if (6 * tCol < 1) return normalizedTo8Bit(t2 + (t1 - t2) * 6 * validTCol);
			if (2 * tCol < 1) return normalizedTo8Bit(t1);

			// 3 * validTCol < ~2.  Last one.  Could be over 2 due to rounding
			return normalizedTo8Bit(t2 + (t1 - t2) * (2 / 3 - validTCol) * 6);
		}

		if (hsl.s === 0) {
			//Grey
			const colVal = normalizedTo8Bit(hsl.l);
			return {
				r: colVal,
				g: colVal,
				b: colVal,
				a: hsl.a || 1
			};
		}

		const t1 = hsl.l < 0.5 ? hsl.l * (1 + hsl.s) : hsl.l + hsl.s - hsl.l * hsl.s;
		const t2 = 2 * hsl.l - t1;

		const hue = hsl.h / 360;

		const tR = hue + 1 / 3;
		const tG = hue;
		const tB = hue - 1 / 3;

		return {
			r: toRGBVal(t1, t2, tR),
			g: toRGBVal(t1, t2, tG),
			b: toRGBVal(t1, t2, tB),
			a: hsl.a || 1
		};
	}

	function rgb2hsl(rgb) {
		const rgbMin = Math.min(rgb.r, rgb.g, rgb.b);
		const rgbMax = Math.max(rgb.r, rgb.g, rgb.b);
		const rgbDelta = rgbMax - rgbMin;

		const l = (rgbMax + rgbMin) / 510;

		// Grey
		if (rgbDelta === 0) {
			return {
				h: 0,
				s: 0,
				l,
				a: rgb.a || 1
			};
		}

		const s =
			l < 0.5
				? rgbDelta / (rgbMax + rgbMin)
				: rgbDelta / (510 - rgbMax - rgbMin);

		let h = 0;
		if (rgb.r === rgbMax) h = (rgb.g - rgb.b) / rgbDelta;
		else if (rgb.g === rgbMax) h = 2 + (rgb.b - rgb.r) / rgbDelta;
		else h = 4 + (rgb.r - rgb.g) / rgbDelta;

		h *= 60; //To degrees

		return {
			h,
			s,
			l,
			a: rgb.a || 1
		};
	}

	function hsv2rgba(hsv) {
		const {h: actualH, s, v: actualV, a} = hsv;
		
		const h = actualH / 60;
		const hi = Math.floor(h) % 6;

		const f = h - Math.floor(h);
		const p = 255 * actualV * (1 - s);
		const q = 255 * actualV * (1 - (s * f));
		const t = 255 * actualV * (1 - (s * (1 - f)));
		const v = actualV * 255;

		let r;
		let g;
		let b;

		switch (hi) {
			case 0:
				r = v;
				g = t;
				b = p;
				break;
			case 1:
				r = q;
				g = v;
				b = p;
				break;
			case 2:
				r = p;
				g = v;
				b = t;
				break;
			case 3:
				r = p;
				g = q;
				b = v;
				break;
			case 4:
				r = t;
				g = p;
				b = v;
				break;
			case 5:
				r = v;
				g = p;
				b = q;
				break;
		}

		return {
			r: Math.round(r),
			g: Math.round(g),
			b: Math.round(b),
			a: isUndefinedOrNull(a) ? 1 : a
		};
	}

	function rgba2hsv({
		r: actualR,
		g: actualG,
		b: actualB,
		a
	}) {
		let rdif;
		let gdif;
		let bdif;
		let h;
		let s;

		const r = actualR / 255;
		const g = actualG / 255;
		const b = actualB / 255;

		const v = Math.max(r, g, b);
		const diff = v - Math.min(r, g, b);

		const diffc = function (c) {
			return (v - c) / 6 / diff + 1 / 2;
		};

		if (diff === 0) {
			h = 0;
			s = 0;
		} else {
			s = diff / v;
			rdif = diffc(r);
			gdif = diffc(g);
			bdif = diffc(b);

			if (r === v) 
				h = bdif - gdif;
			else if (g === v) 
				h = (1 / 3) + rdif - bdif;
			else if (b === v) 
				h = (2 / 3) + gdif - rdif;
		

			if (h < 0) 
				h += 1;
			else if (h > 1) 
				h -= 1;
		}

		return {
			h: h * 360,
			s,
			v,
			a: isUndefinedOrNull(a) ? 1 : a
		};
	}

	/**
	 * Converts to a 2 character hex value
	 * @param {number} decVal
	 */
	function toHex(decVal) {
		const decVal8Bit = enforce8BitRange(decVal);
		const hexVal = decVal8Bit.toString(16);
		return hexVal.length < 2 ? `0${hexVal}` : hexVal;
	}

	function rgbHexFormat({r, g, b, a}) {
		let retVal = `#${toHex(r)}${toHex(g)}${toHex(b)}`;
		if (a >= 0 && a < 1)
			retVal += toHex(a * 255);

		return retVal;
	}

	return {
		/**
		 * Return a colour which can be used in browser
		 * Very simple at the moment.  Needs to understand more.
		 */
		getColor() {
			// Otherwise, to hex then check the for standard colours
			let rgbHex = this.rgbHex();
			if (this.enumerateNames)
				rgbHex = REV_W3C_COLOR[rgbHex] || rgbHex;

			return rgbHex;
		},

		/**
		 * Set the colour from an HTML color
		 * Very simple at the moment.  Needs to understand more.
		 */
		setColor(color) {
			if (!color)
				return this;

			// Deal with named colours
			let newColor = color.toLowerCase();
			newColor = W3C_COLOR[newColor] || newColor;

			let colSep = null;
			CONVERT.evalOrder.some(key => {
				const matchArr = CONVERT[key].p.exec(newColor);
				if (!matchArr) return false;

				colSep = CONVERT[key].f(matchArr);

				if (!colSep) return false;

				return true;
			});

			if (!colSep)
				return null;

			return this.setRGB(colSep);
		},

		/**
		 * Returns CSS rgba(r,g,b,a) format
		 */
		rgba() {
			return (`rgba(${this.r}, ${this.g}, ${this.b}, ${this.a.toFixed(3)})`);
		},

		/**
		 * Returns 6 or 8 byte hex rgb format depending on alpha value
		 */
		rgbHex() {
			return rgbHexFormat(this);
		},

		/**
		 * Returns the colour ignoring the alpha value
		 */
		rgbHexNoAlpha() {
			const rgb = this.getRGB();
			rgb.a = 1;
			return rgbHexFormat(rgb);
		},

		/**
		 * Sets the colour from an RGB values object.
		 * r,g and b values should be in the range 0-255. a, 0-1
		 * @param {{r:number,g:number,b:number, a:number}} rgb
		 */
		setRGB(rgb) {
			this.r = enforce8BitRange(rgb.r);
			this.g = enforce8BitRange(rgb.g);
			this.b = enforce8BitRange(rgb.b);
			this.a = enforceNormalizedRange(isUndefinedOrNull(rgb.a) ? 1 : rgb.a);
			return this;
		},

		/**
		 * Returns the color as an RGB object
		 * @returns {{r: number, g: number, b: number, a:number}}
		 */
		getRGB() {
			return {
				r: this.r,
				g: this.g,
				b: this.b,
				a: this.a
			};
		},

		/**
		 * Returns is light using YIQ calc
		 * @returns true|false
		 */
		isLightYIQ() {
			const rgb = this.getRGB();
			return ((rgb.r * 299) + (rgb.g * 587) + (rgb.b * 114)) / 1000 >= 128;
		},

		/**
		 * Returns is light using Luminance calc
		 * @returns true|false
		 */
		isLightLuminance() {
			const rgb = this.getRGB();
			return ((rgb.r * 0.2126) + (rgb.g * 0.7152) + (rgb.b * 0.0722)) >= 110;
		},

		/**
		 * Returns is light using percent calc
		 * @returns true|false
		 */
		isLightPercent() {
			return (parseInt(this.rgbHex(), 16) > 0xffffff / 2);
		},

		/**
		 * Returns is light using Alpha of lower than 50%
		 * @returns true|false
		 */
		isLightAlpha() {
			const rgb = this.getRGB();
			return rgb.a < 0.5;
		},

		/**
		 * Returns true if color is light according to YIQ
		 * @returns true|false
		 */
		isLight() {
			return this.isLightAlpha() || (this.isLightYIQ() && this.isLightLuminance());
		},

		/**
		 * Sets the colour from an HSL values object.
		 * Ranges: h: 0-360, s: 0-1, l: 0-1, a: 0-1
		 * @param {{h:number,s:number,l:number,a:number}} hsl
		 */
		setHSL(hsl) {
			const rgb = hsl2rgb(hsl);
			this.r = rgb.r;
			this.g = rgb.g;
			this.b = rgb.b;
			this.a = hsl.a || 1;
			return this;
		},

		getHSL() {
			return rgb2hsl(this);
		},

		/**
		 * Sets the colour from an HSV values object.
		 * Ranges: h: 0-360, s: 0-1, v: 0-1, a: 0-1
		 * @param {{h:number,s:number,v:number,a:number}} hsv
		 */
		setHSV(hsv) {
			const rgb = hsv2rgba(hsv);
			this.r = rgb.r;
			this.g = rgb.g;
			this.b = rgb.b;
			this.a = hsv.a || 1;
			return this;
		},

		getHSV() {
			return rgba2hsv(this);
		},

		/**
		 * Returns the hue base color (saturation=1, value=0.5, ignore alpha).
		 * Uses color values unless hsv object is provided
		 *
		 * @param {{h:number,s:number,v:number}} hsv
		 */
		rgbBaseHue(hsv = rgba2hsv(this)) {
			return rgbHexFormat(hsl2rgb({h: hsv.h, s: 1, v: 0.5}));
		},

		/**
		 * If getColor should return W3C Color names.
		 */
		enumerateW3CColorNames(enumerateNames) {
			this.enumerateName = enumerateNames;
		},

		/**
		 * Colour utility functions
		 */
		utils: {
			enforce8BitRange,
			enforceDegreeRange,
			enforceNormalizedRange,
			normalizedTo8Bit,
			isUndefinedOrNull,
			hsl2rgb,
			rgb2hsl,
			hsv2rgba,
			rgba2hsv
		}
	};
})();

export default Color;