import {SAVE_BUTTON_CONFIG, CANCEL_BUTTON_CONFIG} from '../constants';

const manageDisabled = ({
	buttonConfig,
	initialColorHex,
	selectedColorHex,
}) => {
	const newButtonConfig = {...buttonConfig};
	newButtonConfig.disabled = initialColorHex === selectedColorHex;
	return newButtonConfig;
};

export default ({includeButtons, initialColorHex, selectedColorHex}) => {
	const buttonsConfig = [];

	if (!Array.isArray(includeButtons))
		return buttonsConfig;

	includeButtons.forEach(buttonName => {
		if (buttonName === SAVE_BUTTON_CONFIG.value) {
			buttonsConfig.push(
				manageDisabled({
					buttonConfig: SAVE_BUTTON_CONFIG,
					initialColorHex,
					selectedColorHex,
				})
			);
		}

		if (buttonName === CANCEL_BUTTON_CONFIG.value)
			buttonsConfig.unshift(CANCEL_BUTTON_CONFIG);
	});

	return buttonsConfig;
};
