import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {
	DEFAULT_PALETTE,
	DEFAULT_COLOR,
	INTERNAL_COLOR_SET,
	DEFAULT_PALETTE_ACCORDION_ID
} from '../constants';

import {view} from './view';
import styles from './_styles.scss';

/**
 * Color picker that allows users to select a color from
 * a grid of colors.
 *
 * ```jsx
 *
 * <sn-color-picker-swatches selectedColor={color} />
 * ```
 *
 * @seismicElement now-color-picker
 * @summary Color picker that allows users to select a color.
 */
createCustomElement('sn-color-picker-swatches', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {
		locals: {
			targetRef: []
		},
	},
	properties: {
		paletteColors: {default: DEFAULT_PALETTE},
		selectedColorHex: {default: DEFAULT_COLOR},
		showCustomColors: {default: true},
		customColors: {default: []},
		expandedAccordion: {default: DEFAULT_PALETTE_ACCORDION_ID}
	},
	actionHandlers: {},
	dispatches: {
		/**
		 * Action dispatched when a color is selected. The payload will contain
		 * the color selected.
		 *
		 * @type {{value: string}}
		 */
		[INTERNAL_COLOR_SET]: {}
	}
});
