import {VALID_TABS, SWATCHES_TAB_CONFIG, LIST_TAB_CONFIG, PALLETTE_TAB_CONFIG} from '../constants';

export default (tabsToDisplay, showCustomColors) => {
	const tabsConfig = [];
	let isPaletteAdded = false;

	tabsToDisplay.forEach(tabName => {
		switch (tabName) {
		case VALID_TABS[0]:
			tabsConfig.push(SWATCHES_TAB_CONFIG);
			break;
		case VALID_TABS[1]:
			tabsConfig.push(LIST_TAB_CONFIG);
			break;
		case VALID_TABS[2]:
			tabsConfig.push(PALLETTE_TAB_CONFIG);
			isPaletteAdded = true;
			break;
		default:
			console.info(`Invalid color picker tab name: ${tabName}`);
		}
	});

	if (showCustomColors && !isPaletteAdded)
		tabsConfig.push(PALLETTE_TAB_CONFIG);

	return tabsConfig;
};
