import {INTERNAL_COLOR_SET} from '../../constants';

import calculateLensPosition from './calculateLensPosition';
import getA11yMessage from './getA11yMessage';

// Takes the coordinates from the event, works out the location on the SL plane
const setSLValue = ({path, target, pageX, pageY, offsetX, offsetY}) => {
	let slPlane = target.getBoundingClientRect();
	if (path) 
		slPlane = path[0].getBoundingClientRect();

	const actualX = offsetX;
	const actualY = offsetY;

	const width = slPlane.width | 0;
	const height = slPlane.height | 0;

	const value = (height - actualY) / height;
	const saturation = actualX / width;

	return {
		value,
		saturation
	};
};

export default (event, h, a, dispatch, updateState) => {
	event.preventDefault();

	const {value: v, saturation: s} = setSLValue(event);
	const lens = calculateLensPosition(v, s);

	updateState({
		lens,
		a11yLiveMessage: getA11yMessage(lens)
	});
	dispatch(INTERNAL_COLOR_SET, {value: {h, s, v, a}});
};
