import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {t} from 'sn-translate';

import '@servicenow/now-tooltip';

import {INTERNAL_FOCUS_SET} from '../constants';

import setValue from './utils/setValue';
import getHueFamily from './utils/getHueFamily';

export default (
	{
		properties: {variant, selectedColorHsv, selectedColorRgba, autoFocus},
		hueFamily,
		a11yMessage,
		targetRef
	},
	{dispatch, updateState}
) => {
	const setFocus = ref => setTimeout(
		() => {
			updateState({targetRef: ref});
			if (ref && autoFocus) {
				ref.focus();
				dispatch(INTERNAL_FOCUS_SET);
			}
		}
	);

	const {r, g, b} = selectedColorRgba;
	const valueKey = variant.charAt(0);
	const isHue = variant === 'hue';

	const inputStyle = !isHue ?
		{
			background: `linear-gradient(to right, rgba(${r}, ${g}, ${b}, 0), rgba(${r}, ${g}, ${b}, 1))`
		} : {};

	const roundedValue = Math.round(selectedColorHsv[valueKey] * (isHue ? 1 : 100));

	let currentHueFamily = hueFamily;
	if (isHue && !currentHueFamily)
		currentHueFamily = getHueFamily(roundedValue);

	return <div className={`color-picker-hsv-slider color-picker-hsv-slider--${variant}`}>
		<input
			aria-label={variant}
			className={`color-picker-hsv-slider__input color-picker-hsv-slider__input--${variant}`}
			type="range"
			min="0"
			max={isHue ? '360' : '100'}
			name={variant}
			value={roundedValue}
			style={inputStyle}
			on-input={event => {
				event.preventDefault();
        
				const newValue = event.currentTarget.value;
				setValue({newValue, selectedColorHsv, selectedColorRgba, valueKey, dispatch, updateState});
			}}
			hook-insert={({elm}) => setFocus(elm)}
			hook-destroy={() => setFocus(null)}
		/>
		<now-tooltip
			target-ref={targetRef}
			content={
				isHue ? 
					t('{0} hue family', currentHueFamily) :
					t('Alpha {0}%', roundedValue)
			}
		/>
		<div role="region" ariaLive="assertive" className="a11y">
			{a11yMessage || ''}
		</div>
	</div>;
};
