import {INTERNAL_COLOR_SET} from '../../constants';

const isRgbPartValid = value => {
	const parsedInt = parseInt(value);
	if (isNaN(parsedInt) || parsedInt < 0 || parsedInt > 255)
		return false;

	return true;
};

const isAPartValid = value => {
	const parsedInt = parseInt(value);
	return !(isNaN(parsedInt) || parsedInt < 0 || parsedInt > 100);
};

const isRgbaValid = ({r, g, b, a}) => isRgbPartValid(r) && isRgbPartValid(g) && isRgbPartValid(b) && isAPartValid(a);

export default ({value, selectedColorRgba, key, dispatch}) => {
	if (selectedColorRgba[key] === value || selectedColorRgba[key] === parseInt(value))
		return;

	const rgbaState = {...selectedColorRgba};
	rgbaState[key] = value;

	if (isRgbaValid(rgbaState)) {
		if (key === 'a')
			rgbaState.a = rgbaState.a / 100;
		// Convert any string numbers into floats for rgb and a
		Object.keys(rgbaState).forEach(
			key => rgbaState[key] = parseFloat(rgbaState[key])
		);
		dispatch(INTERNAL_COLOR_SET, {value: rgbaState});
	}
};
