import Color from '../../color';

export default colors => {
	const validColors = [];
  
	for(let i = 0; i < colors.length; i++) {
		const color = new Color();
		const isValid = color.setColor(colors[i].value);

		if(isValid) {
			const colorObject = {...colors[i], value: color.getColor().toUpperCase()};
			validColors.push(colorObject);
		} else {
			console.info(`Color Picker: Color HEX ${colors[i].value} is not valid. It was omitted from swatched and list views.`);
		}
	}
  
	return validColors;
};
