import '@servicenow/now-icon';
import '@servicenow/now-tooltip';

import {Fragment} from '@servicenow/ui-renderer-snabbdom';

export const view = state => {
	const {
		buttons,
		properties: {initialColorHex, selectedColorHex}
	} = state;

	return (
		<Fragment>
			{(buttons && buttons.length > 0) ?
				<div id="cpButtonsPanel" className="color-picker-buttons__panel">
					{buttons.map(({label, variant, actionName, disabled}) => (
						<now-button
							label={label}
							variant={variant}
							disabled={disabled}
							className="color-picker-buttons__panel__button"
							size="md"
							append-to-payload={{actionName, initialColorHex, selectedColorHex}}
						/>
					))}
				</div>
				: null}
		</Fragment>
	);
};