import isNonModifier from './isNonModifier';
import getNewValue from './getNewValue';

const hasSpace = newColorValue => newColorValue.indexOf(' ') > -1;

const isValidValue = ({newColorValue, min, max}) =>
	newColorValue >= min && newColorValue <= max;

export default ({event, key: fieldName, min, max}) => {
	const {key, target} = event;
	if (isNonModifier(event)) {
		const newColorValue = getNewValue(key, target, fieldName);

		if (!isValidValue({newColorValue, fieldName, min, max}) || hasSpace(newColorValue))
			event.preventDefault();
	}
};
