import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {t} from 'sn-translate';

import '@servicenow/now-tooltip';

import transformHEXForA11y from '../utils/transformHEXForA11y';

export default (
	{
		properties: {
			selectedColorHex,
		},
		targetRef
	},
	{updateState}
) => {
	const setTargetRef = ref => setTimeout(() => updateState({targetRef: ref}));

	return (
		<Fragment>
			<div
				style={{'background-color': selectedColorHex}}
				className="color-picker-swatch"
				hook-insert={({elm}) => setTargetRef(elm)}
				hook-destroy={() => setTargetRef(null)}
				tabindex={0}
				ariaLabel={t('Currently selected color HEX: {0}', transformHEXForA11y(selectedColorHex))}
			/>
			<now-tooltip
				target-ref={targetRef}
				content={selectedColorHex}
			/>
		</Fragment>
	);
};
