import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {announceColorAdded} from './effects';
import view from './view';
import styles from './_styles.scss';

const defaultColor = '#278EFC';

/**
 * Palette panel for color picker.
 *
 * ```jsx
*
* <sn-color-picker-palette selectedColorHex={hex} selectedColorHsv={hsv} selectedColorRgba={rgba} />
* ```
*
* @seismicElement sn-color-picker-palette
* @summary Palette panel for color picker.
*/
createCustomElement('sn-color-picker-palette', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {
		selectedFieldsType: 'rgba',
		locals: {
			targetRef: []
		},
		customColorsLegendTargetRef: null,
		customColorsDeleteTargetRef: null,
		addCustomColorButtonRef: null,
		colorAddedMessage: ''
	},
	properties: {
		selectedColorHex: {default: defaultColor},
		selectedColorHsv: {
			default: {
				h: 0,
				s: 0,
				v: 0,
				a: 1,
			}
		},
		selectedColorRgba: {
			default: {
				r: 0,
				g: 0,
				b: 0,
				a: 1,
			}
		},
		paletteColors: {
			default: []
		},
		showCustomColors: {
			default: false
		},
		customColors: {
			default: []
		},
		focusFirstElement: {
			default: false
		}
	},
	actionHandlers: {
		'NOW_BUTTON_ICONIC#CLICKED': announceColorAdded
	},
	dispatches: {}
});
