export default (key, target, fieldName) => {
	// Unfortunately we are unable to consistently get actual input from event in all browsers
	const input = target.shadowRoot.querySelector(`input[name=${fieldName}]`);
	const {value, selectionStart: cursorPosition} = input;

	const newColorValue = [value.slice(0, cursorPosition), key, value.slice(cursorPosition)].join('');
  
	return newColorValue;
};
