import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {INTERNAL_COLOR_SET} from '../constants';

import {updatePicker} from './effects';
import view from './view';
import styles from './_styles.scss';

const defaultColor = '#278EFC';

/**
 * Saturation and value picker for color picker.
 *
 * ```jsx
*
* <sn-color-picker-saturation-value selectedColorHsv={hsv} />
* ```
*
* @seismicElement sn-color-picker-saturation-value
* @summary Saturation and value picker for color picker.
*/
createCustomElement('sn-color-picker-saturation-value', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {
		lens: {x: null, y: null},
		baseColor: defaultColor,
		updateLensOnPropChange: true,
		a11yLiveMessage: ''
	},
	properties: {
		selectedColorHsv: {
			default: {
				h: 0,
				s: 0,
				v: 0,
				a: 0
			}
		},
		selectedColorHex: {
			default: '#000000'
		},
		autoFocus: {
			default: false
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_CONNECTED]: updatePicker,
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: updatePicker
	},
	dispatches: {
		[INTERNAL_COLOR_SET]: {}
	}
});
