/* istanbul ignore file */
import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {DEFAULT_COLOR} from '../constants';
import processButtonClick from './utils/processButtonClick';

import {
	NOW_COLOR_PICKER_SAVE_CLICKED,
	NOW_COLOR_PICKER_CANCEL_CLICKED,
} from './constants';

import {handleInitialProperties, handlePropertyChanges} from './effects';
import {view} from './view';
import styles from './_styles.scss';

/**
 * Buttons for Save and Cancel events
 *
 * ```jsx
 *
 * <sn-color-picker-buttons includeButtons={buttons} selectedColorHex={button} initialColorHex={button} />
 * ```
 *
 * @seismicElement now-color-picker
 * @summary Color picker that allows users to select a color.
 */
createCustomElement('sn-color-picker-buttons', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		includeButtons: {default: []},
		selectedColorHex: {default: DEFAULT_COLOR},
		initialColorHex: {default: DEFAULT_COLOR}
	},
	initialState: {
		buttons: []
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: handleInitialProperties,
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: handlePropertyChanges,
		'NOW_BUTTON#CLICKED': processButtonClick
	},
	dispatches: {
		/**
		 * Action dispatched when a button is clicked. The payload will contain
		 * the selected color string in hex format e.g. #278EFC if the save
		 * button is clicked and the initial color string in hex format if the
		 * cancel button is clicked
		 *
		 * @type {{value: string}}
		 */
		[NOW_COLOR_PICKER_SAVE_CLICKED]: {},
		[NOW_COLOR_PICKER_CANCEL_CLICKED]: {}
	}
});
