import {NOW_COLOR_PICKER_SAVE_CLICKED, NOW_COLOR_PICKER_CANCEL_CLICKED} from '../constants';

export default ({action, dispatch}) => {
	action.preventDefault();
	const {payload: {selectedColorHex, initialColorHex, actionName}} = action;

	let value = '';
	if (actionName) {
		switch (actionName) {
			case NOW_COLOR_PICKER_SAVE_CLICKED:
				value = selectedColorHex;
				break;
			case NOW_COLOR_PICKER_CANCEL_CLICKED:
				value = initialColorHex;
				break;
		}
	}
	if (value)
		dispatch(actionName, {value});
};
