import {t} from 'sn-translate';

import '@servicenow/now-icon';
import '@servicenow/now-accordion';
import '@servicenow/now-button';

import {
	SWITCH_TO_CUSTOM_COLOR_SELECTION_ACTION_NAME,
	DELETE_CUSTOM_COLOR_ACTION_NAME,
	DEFAULT_PALETTE_ACCORDION_ID,
	CUSTOM_COLORS_ACCORDION_ID
} from '../constants';
import renderTooltip from '../utils/renderTooltip';
import transformHEXForA11y from '../utils/transformHEXForA11y';
import isColorLight from '../utils/isColorLight';

export const handleColorSelection = (color, dispatch) => {
	dispatch('NOW_COLOR_PICKER#INTERNAL_COLOR_SET', {
		value: color,
	});
};

const renderList = ({
	colors,
	selectedColor,
	showDelete,
	setTargetRef,
	indexOffset,
	dispatch,
	state,
	isCustom
}) => (
	colors ? colors.map(({displayValue: colorAlias, value: color}, index ) => {
		const {addCustomColorButtonRef} = state;

		const colorLabel = colorAlias ? `${colorAlias} (${color})` : color;

		const a11yPaletteColor = transformHEXForA11y(color);
		const a11yColorLabel = colorAlias ? `${colorAlias} (${a11yPaletteColor})` : a11yPaletteColor;

		const contrastClass = isColorLight(color) ? 'color-picker__swatch__selected-icon__dark' : 'color-picker__swatch__selected-icon__light';

		const refIndex = indexOffset ? indexOffset + index : index;

		return (
			<div className="color-picker-list-panel__item">
				<label
					key={color}
					htmlFor={`color-picker-list-${color}`}
					className="color-picker__swatch"
					ariaLabel={a11yColorLabel}
					hook-insert={({elm}) => setTargetRef(elm, refIndex)}
					hook-destroy={() => setTargetRef(null, refIndex)}
				>
					<div className="color-picker-list-panel__item__input-wrapper">
						<input
							type="radio"
							name={`color-picker-list${isCustom ? '-custom' : ''}`}
							id={`color-picker-list-${color}`}
							className="color-picker__swatch__radio"
							value={color}
							onchange={() => handleColorSelection(color, dispatch)}
							checked={selectedColor === color ? 'true' : null}
							style={{'background-color': color}}
						/>
					</div>
					<div className="color-picker__swatch__label">{colorLabel}</div>
					{selectedColor === color ? (<now-icon icon="check-outline" size="sm" className={contrastClass} />) : ('')}
					{renderTooltip(state, colorLabel, refIndex)}
				</label>
				{showDelete ?
					<now-button-iconic
						icon="trash-outline"
						tooltipContent={t('Delete {0}', color)}
						size="md"
						variant="tertiary"
						bare={true}
						id={`color-picker-delete-${color.replace('#', '')}`}
						configAria={{button:{'aria-label': t('Delete {0}', transformHEXForA11y(color))}}}
						append-to-payload={{
							actionName: DELETE_CUSTOM_COLOR_ACTION_NAME,
							value: color
						}}
						on-click={() => addCustomColorButtonRef.focus()}
					/>
					: ''}
			</div>
		);
	}) : ''
);

export const view = (state, {dispatch, updateState}) => {
	const {
		locals: {targetRef},
		properties: {paletteColors, selectedColorHex, showCustomColors, customColors, expandedAccordion},
	} = state;

	const setTargetRef = (ref, index) => {
		targetRef[index] = ref;

		setTimeout(() => updateState({targetRef}));
	};

	const setAddCustomColorButtonRef = ref => setTimeout(() => updateState({addCustomColorButtonRef: ref}));

	return (
		<div id="cpListPanel" class={{
			'color-picker-list-panel': true,
			'color-picker-list-panel--no-padding': showCustomColors,
		}}>
			{showCustomColors ?
				<now-accordion
					expandSingle={true}
					headingLevel="2"
					triggerIcon={{type: 'chevron', position: 'end'}}
					className="color-picker-list-panel__accordion"
				>
					<now-accordion-item
						header={{label: t('Category Colors')}}
						expanded={expandedAccordion === DEFAULT_PALETTE_ACCORDION_ID}
						component-id={DEFAULT_PALETTE_ACCORDION_ID}
						append-to-payload={{view: 'list'}}
					>
						<div
							slot="content"
							className="color-picker-list__group-container"
						>
							{renderList({
								colors: paletteColors,
								selectedColor: selectedColorHex,
								showDelete: false,
								setTargetRef, 
								dispatch,
								state,
								isCustom: false
							})}
						</div>
					</now-accordion-item>
					<now-accordion-item
						header={{label: t('Custom Colors')}}
						expanded={expandedAccordion === CUSTOM_COLORS_ACCORDION_ID}
						component-id={CUSTOM_COLORS_ACCORDION_ID}
						append-to-payload={{view: 'list'}}
					>
						<div
							slot="content"
							className="color-picker-list__group-container"
						>
							<div className="color-picker__swatch">
								<now-button-iconic
									icon="plus-outline"
									tooltipContent={t('Add Custom Color')}
									size="md"
									variant="tertiary"
									configAria={{button:{'aria-label': t('Add Custom Color')}}}
									append-to-payload={{actionName: SWITCH_TO_CUSTOM_COLOR_SELECTION_ACTION_NAME}}
									hook-insert={({elm}) => setAddCustomColorButtonRef(elm)}
									hook-destroy={() => setAddCustomColorButtonRef(null)}
								/>
								<div className="color-picker__swatch__label" aria-hidden="true">{t('Add Custom Color')}</div>
							</div>
							{renderList({
								colors: customColors,
								selectedColor: selectedColorHex,
								showDelete: true,
								setTargetRef,
								indexOffset: paletteColors.length,
								dispatch,
								state,
								isCustom: true
							})}
						</div>
					</now-accordion-item>
				</now-accordion>
				:
				<div className="color-picker-list__group-container">
					{renderList({
						colors: paletteColors,
						selectedColor: selectedColorHex,
						showDelete: false,
						setTargetRef,
						dispatch,
						state,
						isCustom: false
					})}
				</div>
			}
		</div>
	);
};
