import isEqual from 'lodash/isEqual';

import {
	DEFAULT_COLOR,
	ADD_CUSTOM_COLOR_ACTION_NAME,
	SWITCH_TO_CUSTOM_COLOR_SELECTION_ACTION_NAME,
	DELETE_CUSTOM_COLOR_ACTION_NAME,
	DEFAULT_PALETTE_ACCORDION_ID,
	CUSTOM_COLORS_ACCORDION_ID
} from '../constants';
import Color from '../color';

import validateColors from './utils/validateColors';
import generateTabsConfig from './utils/generateTabsConfig';
import {PALETTE_TAB_ID} from './constants';

export const handleInitialProperties = ({
	updateState,
	state: {
		properties: {palette, includeTabs, includeButtons, selectedColor: selectedColorHex, showCustomColors, customColors},
		selectedTab: currentlySelectedTab,
		initialColor
	},
	newSelectedColor
}) => {
	const tabsConfig = generateTabsConfig(includeTabs, showCustomColors);

	const selectedTab = currentlySelectedTab || (tabsConfig.length ? tabsConfig[0].id : null);

	let initialColorValue = initialColor;
	if (!initialColorValue || newSelectedColor) {
		const firstDefaultColor = palette && palette[0] && palette[0].value;
		initialColorValue = newSelectedColor || selectedColorHex || firstDefaultColor || DEFAULT_COLOR;
	}

	const color = new Color();
	color.setColor(initialColorValue);

	updateState({
		tabs: tabsConfig,
		selectedTab,
		buttons: includeButtons || [],
		initialColor: initialColorValue,
		selectedColor: {
			hex: initialColorValue,
			rgba: color.getRGB(),
			hsv: color.getHSV()
		},
		validatedPalette: validateColors(palette),
		stateCustomColors: validateColors([...customColors])
	});
};

export const handlePropertyChanges = ({
	updateState,
	state,
	action: {
		payload: {name, value}
	}
}) => {
	if (name === 'palette' || name === 'includeTabs' || name === 'includeButtons' || name === 'showCustomColors' || name === 'customColors')
		handleInitialProperties({updateState, state});
	else if(name === 'selectedColor') 
		handleInitialProperties({updateState, state, newSelectedColor: value});
};

export const handleTabSelection = ({
	updateState,
	action: {
		payload: {value: selectedTab}
	}
}) => updateState({selectedTab});

export const handleColorChange = ({
	updateState,
	dispatch,
	state: {
		selectedColor: {hsv}
	},
	action: {
		payload: {
			value: newColorCode
		}
	}
}) => {
	let newColorHsv = newColorCode;

	const newColor = new Color();

	if (typeof newColorCode === 'string') {
		newColor.setColor(newColorCode);
		newColorHsv = newColor.getHSV();
	} else if (typeof newColorCode === 'object' && Object.keys(newColorCode).includes('r')) {
		newColor.setRGB(newColorCode);
		newColorHsv = newColor.getHSV();
	} else {
		newColor.setHSV(newColorCode);
	}

	if (!isEqual(hsv, newColorHsv)) {
		const newHex = newColor.getColor().toUpperCase();
		dispatch('NOW_COLOR_PICKER#SELECTED_COLOR_SET', {value: newHex});
		updateState({
			selectedColor: {
				hex: newHex,
				rgba: newColor.getRGB(),
				hsv: newColorHsv
			}
		});
	}
};

export const handleCustomColor =  ({
	state: {stateCustomColors},
	action: {
		payload: {
			value: color,
			actionName
		}
	},
	dispatch,
	updateState
}) => {
	let newCustomColors;
	switch(actionName){
		case SWITCH_TO_CUSTOM_COLOR_SELECTION_ACTION_NAME:
			updateState({
				selectedTab: PALETTE_TAB_ID,
				focusFirstPaletteElement: true
			});
			break;
		case ADD_CUSTOM_COLOR_ACTION_NAME:
			newCustomColors = [...stateCustomColors, color];

			updateState({stateCustomColors: newCustomColors});
			dispatch('NOW_COLOR_PICKER#CUSTOM_COLOR_ADDED', {value: color.value});
			break; 
		case DELETE_CUSTOM_COLOR_ACTION_NAME:
			newCustomColors = stateCustomColors.filter(({value}) => value !== color);

			updateState({stateCustomColors: newCustomColors});
			dispatch('NOW_COLOR_PICKER#CUSTOM_COLOR_DELETED', {value: color});
			break;
	}
};

export const handleAccordionItemExpansion = ({
	action: {
		payload: {value: isExpanded, componentId, view}
	},
	updateState
}) => {
	const stateKey = view === 'swatches' ? 'expandedSwatchesAccordion' : 'expandedListAccordion';

	if (
		(!isExpanded && componentId === DEFAULT_PALETTE_ACCORDION_ID) ||
		(isExpanded && componentId === CUSTOM_COLORS_ACCORDION_ID)
	)
		updateState({[stateKey]: CUSTOM_COLORS_ACCORDION_ID});
	else if (
		(!isExpanded && componentId === CUSTOM_COLORS_ACCORDION_ID) ||
		(isExpanded && componentId === DEFAULT_PALETTE_ACCORDION_ID)
	)
		updateState({[stateKey]: DEFAULT_PALETTE_ACCORDION_ID});
};
