import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {INTERNAL_COLOR_SET} from '../constants';

import view from './view';
import styles from './_styles.scss';

/**
 * Alternative alpha control for color picker.
 *
 * ```jsx
*
* <sn-color-picker-alpha selectedColorRgba={color} selectedColorHsv={color} />
* ```
*
* @seismicElement sn-color-picker-alpha
* @summary Alternative alpha control for color picker.
*/
createCustomElement('sn-color-picker-hsv-slider', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {
		targetRef: null,
		hueFamily: null,
		a11yMessage: null
	},
	properties: {
		variant: {
			default: 'hue'
		},
		selectedColorRgba: {
			default: {
				r: 0,
				g: 0,
				b: 0,
				a: 0
			}
		},
		selectedColorHsv: {
			default: {
				h: 0,
				s: 0,
				v: 0,
				a: 0
			}
		},
		autoFocus: {
			default: false
		}
	},
	actionHandlers: {},
	dispatches: {
		[INTERNAL_COLOR_SET]: {}
	}
});
