import {t} from 'sn-translate';

const isNumInRange = (num, min, max) => num >= min && num <= max;

export default hue => {
	if(isNumInRange(hue, 0, 29) || isNumInRange(hue, 330, 360))
		return t('Red');
  
	if(isNumInRange(hue, 30, 89))
		return t('Yellow');
  
	if(isNumInRange(hue, 90, 149))
		return t('Green');
    
	if(isNumInRange(hue, 150, 209))
		return t('Cyan');
  
	if(isNumInRange(hue, 210, 269))
		return t('Blue');
  
	if(isNumInRange(hue, 270, 329))
		return t('Magenta');
  
	return '';
};
