import {t} from 'sn-translate';

import '@servicenow/now-dropdown';
import '@servicenow/now-label-value';
import '@servicenow/now-button';
import '@servicenow/now-tooltip';

import '../sn-color-picker-saturation-value';
import '../sn-color-picker-hsv-slider';
import '../sn-color-picker-color-fields';
import '../sn-color-picker-swatch';
import {ADD_CUSTOM_COLOR_ACTION_NAME, DELETE_CUSTOM_COLOR_ACTION_NAME} from '../constants';

import renderSwatches from '../utils/renderSwatches';
import transformHEXForA11y from '../utils/transformHEXForA11y';

const addCustomColorMessage = t('Add to Custom Colors');
const unableToAddCustomColorMessage = t('This color is already added to default or custom colors');
const selectToDeleteMessage = t('Select a swatch to delete');

const isColorInPalette = (color, palette) => palette.findIndex(({value}) => value === color) !== -1;

export default (
	state,
	{dispatch, updateState}
) => {
	const {
		locals: {targetRef},
		customColorsLegendTargetRef,
		customColorsDeleteTargetRef,
		addCustomColorButtonRef,
		colorAddedMessage,
		properties: {
			selectedColorHex,
			selectedColorHsv,
			selectedColorRgba,
			paletteColors,
			showCustomColors,
			customColors,
			focusFirstElement
		}
	} = state;

	const setTargetRef = (ref, key) => {
		setTimeout(() => updateState({[key]: ref}));
	};

	const setSwatchesTargetRef = (ref, index) => {
		targetRef[index] = ref;

		setTimeout(() => updateState({targetRef}));
	};

	const isSelectedColorInPalettes = isColorInPalette(selectedColorHex, [...paletteColors, ...customColors]);
	const isSelectedColorCustom = isColorInPalette(selectedColorHex, customColors);

	const deleteMessage = isSelectedColorCustom ? t('Delete {0} from custom colors', selectedColorHex) : selectToDeleteMessage;
	const a11yDeleteMessage = isSelectedColorCustom ? t('Delete {0} from custom colors', transformHEXForA11y(selectedColorHex)) : selectToDeleteMessage; 

	const addCustomColorButtonMessage = showCustomColors && isSelectedColorInPalettes ? unableToAddCustomColorMessage : addCustomColorMessage;

	return <div id="cpAdvancedPanel" className="color-picker-hsv" role="tabpanel">
		<sn-color-picker-saturation-value
			selectedColorHsv={selectedColorHsv}
			selectedColorHex={selectedColorHex}
			className="color-picker-hsv__saturation-variable"
			autoFocus={focusFirstElement}
		/>
		<div className="color-picker-hsv__hue-alpha-swatch">
			<div className="color-picker-hsv__hue-alpha-swatch__sliders">
				<sn-color-picker-hsv-slider
					variant="hue"
					selectedColorHsv={selectedColorHsv}
					selectedColorRgba={selectedColorRgba}
					className="color-picker-hsv__hue"
				/>
				<sn-color-picker-hsv-slider
					variant="alpha"
					selectedColorHsv={selectedColorHsv}
					selectedColorRgba={selectedColorRgba}
					className="color-picker-hsv__alpha"
				/>
			</div>
			<sn-color-picker-swatch selectedColorHex={selectedColorHex} className="color-picker-hsv__color-fields__swatch" />
		</div>
		<sn-color-picker-color-fields
			selectedColorRgba={selectedColorRgba}
			selectedColorHex={selectedColorHex}
			className="color-picker-hsv__color-fields"
		/>
		{showCustomColors ?
			<fieldset className="color-picker-hsv__custom-colors">
				<legend className="color-picker-hsv__custom-colors__legend">
					<div className="color-picker-hsv__custom-colors__legend__inner">
						<now-label-value-inline
							label={t('Custom Colors')}
							value=""
							truncated={true}
							className="color-picker-hsv__custom-colors__legend__label"
							tabindex="0"
							hook-insert={({elm}) => setTargetRef(elm, 'customColorsLegendTargetRef')}
							hook-destroy={() => setTargetRef(null, 'customColorsLegendTargetRef')}
						/>
						<now-tooltip
							content={t('Custom Colors')}
							target-ref={customColorsLegendTargetRef}
						/>
						<div className="color-picker-hsv__custom-colors__legend__delete">
							<now-button-bare
								label={t('Delete')}
								variant="primary"
								size="sm"
								configAria={{'aria-label': a11yDeleteMessage}}
								disabled={!isSelectedColorCustom}
								hidePadding={true}
								className="color-picker-hsv__custom-colors__legend__delete__button"
								append-to-payload={{
									actionName: DELETE_CUSTOM_COLOR_ACTION_NAME,
									value: selectedColorHex
								}}
								on-click={() => setTimeout(() => addCustomColorButtonRef.focus())}
								hook-insert={({elm}) => setTargetRef(elm, 'customColorsDeleteTargetRef')}
								hook-destroy={() => setTargetRef(null, 'customColorsDeleteTargetRef')}
							/>
							<now-tooltip
								content={deleteMessage}
								target-ref={customColorsDeleteTargetRef}
							/>
						</div>
					</div>
				</legend>
				<div className="color-picker-hsv__custom-colors__swatches">
					<now-button-iconic
						icon="plus-outline"
						size="md"
						variant="tertiary"
						configAria={{button:{'aria-label': addCustomColorButtonMessage}}}
						append-to-payload={{
							actionName: ADD_CUSTOM_COLOR_ACTION_NAME,
							value: {value: selectedColorHex}
						}}
						disabled={isSelectedColorInPalettes}
						tabindex={isSelectedColorInPalettes ? 0 : null}
						className="color-picker__swatch"
						hook-insert={({elm}) => setTargetRef(elm, 'addCustomColorButtonRef')}
						hook-destroy={() => setTargetRef(null, 'addCustomColorButtonRef')}
					/>
					<now-tooltip
						content={addCustomColorButtonMessage}
						target-ref={addCustomColorButtonRef}
						position={['top-start']}
					/>
					<div
						className="a11y"
						ariaLive="assertive"
					>
						{colorAddedMessage}
					</div>
					{
						renderSwatches({
							colors: customColors,
							selectedColor: selectedColorHex,
							state,
							dispatch,
							setTargetRef: setSwatchesTargetRef,
							isCustom: true
						})
					}
				</div>
			</fieldset> : ''
		}
	</div>;
};
