import {t} from 'sn-translate';

import transformHEXForA11y from '../utils/transformHEXForA11y';

export const announceColorAdded = ({
	state:{
		properties: {selectedColorHex}
	},
	updateState
}) => {
	updateState({colorAddedMessage: t('Custom color added {0}', transformHEXForA11y(selectedColorHex))});
  
	// This is needed to prevent announcing message when navigating linearly and trigger announcement again
	setTimeout(() => {
		updateState({colorAddedMessage: ''});
	}, 500);
};

