import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {t} from 'sn-translate';

import '@servicenow/now-tooltip';

import hsvSetFromClick from './utils/hsvSetFromClick';
import hsvSetFromKeyboard from './utils/hsvSetFromKeyboard';
import getA11yMessage from './utils/getA11yMessage';

const saturationValueInstructions = t('Saturation-brightness control. Use the left and right arrow keys for saturation and up and down arrow keys for brightness');

export default (
	{
		lens: {x, y},
		baseColor,
		targetRef,
		a11yLiveMessage,
		properties: {
			selectedColorHsv: {h, a},
			selectedColorHex,
			autoFocus
		}
	},
	{
		dispatch,
		updateState
	}
) => {
	const saturation = Math.round(x);
	const brightness = Math.round(100 - y);

	const saturationValueMessage = getA11yMessage({x, y});

	const setTargetRef = ref => setTimeout(
		() => {
			updateState({targetRef: ref});

			if (autoFocus && ref)
				ref.focus();
		}
	);

	return <Fragment>
		<div
			className="color-picker-saturation-value"
			style={{'background-color': baseColor}}
			tabIndex="0"
			on-keydown={
				event => hsvSetFromKeyboard({event, x, y, h, a, dispatch, updateState})
			}
			on-click={
				event => hsvSetFromClick(event, h, a, dispatch, updateState)
			}
			role="widget"
			ariaLabel={`${saturationValueMessage} ${saturationValueInstructions}`}
			hook-insert={({elm}) => setTargetRef(elm)}
			hook-destroy={() => setTargetRef(null)}
			data-saturation={saturation}
			data-brightness={brightness}
		>
			{
				x !== null && y !== null ?
					<i
						className='color-picker-saturation-value__lens'
						style={{
							left: `calc(${x}% - 6px)`,
							top: `calc(${y}% - 6px)`,
							backgroundColor: selectedColorHex
						}}
					/> : ''
			}
		</div>
		<now-tooltip
			target-ref={targetRef}
			content={`${saturationValueMessage}. ${t('Tip: Use the left and right arrow keys for saturation and up and down arrow keys for brightness')}`}
		/>
		<div
			className="a11y"
			ariaLive="assertive"
		>
			{a11yLiveMessage}
		</div>
	</Fragment>;
};
