import isNonModifier from './isNonModifier';
import getNewValue from './getNewValue';

export default (event, fieldName) => {
	const partialHexRegex = /^#{0,1}[0-9a-f]{0,8}$/i;

	const {key, target} = event;
	if (isNonModifier(event)) {
		const newColorValue = getNewValue(key, target, fieldName);

		if (!newColorValue.match(partialHexRegex)) 
			event.preventDefault();
	}
};
