/* istanbul ignore file */
import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import debounce from 'lodash.debounce';

import {INTERNAL_COLOR_SET} from '../constants';

import {HEX} from './constants';
import {colorChange, changeSelectedFieldsType, handleInitialProperties} from '../sn-color-picker-color-fields/effects';
import view from './view';
import styles from './_styles.scss';

const defaultColor = '#278EFC';

/**
 * HEX and RGBA fields with a switch.
 *
 * ```jsx
*
* <sn-color-picker-color-fields selectedColorHex={hex} selectedColorRgba={rgba} />
* ```
*
* @seismicElement sn-color-picker-color-fields
* @summary HEX and RGBA fields with a switch.
*/
createCustomElement('sn-color-picker-color-fields', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {
		selectedFieldsType: HEX,
		fieldValues: {
			rgba: {
				r: 0,
				g: 0,
				b: 0,
				a: 0
			},
			hex: ''
		}
	},
	properties: {
		selectedColorRgba: {
			default: {
				r: 0,
				g: 0,
				b: 0,
				a: 0
			}
		},
		selectedColorHex: {default: defaultColor},
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: handleInitialProperties,
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: handleInitialProperties,
		'NOW_INPUT#INPUT': debounce(colorChange),
		'NOW_DROPDOWN#SELECTED_ITEMS_SET': changeSelectedFieldsType,
	},
	dispatches: {
		/**
		 * Action dispatched when a color is selected. The payload will contain
		 * the color selected.
		 *
		 * @type {{value: string}}
		 */
		[INTERNAL_COLOR_SET]: {}
	}
});
