import {INTERNAL_COLOR_SET} from '../constants';

import transformHEXForA11y from './transformHEXForA11y';
import isColorLight from './isColorLight';
import renderTooltip from './renderTooltip';

export default ({
	colors,
	selectedColor,
	state,
	dispatch,
	setTargetRef,
	indexOffset,
	isCustom
}) => colors ?
	colors.map(({displayValue: colorAlias, value: color}, index) => {
		const colorLabel = colorAlias ? `${colorAlias} (${color})` : color;

		const a11yPaletteColor = transformHEXForA11y(color);
		const a11yColorLabel = colorAlias ? `${colorAlias} (${a11yPaletteColor})` : a11yPaletteColor;

		const contrastClass = isColorLight(color) ? 'color-picker__swatch__selected-icon__dark' : 'color-picker__swatch__selected-icon__light';

		const refIndex = indexOffset ? indexOffset + index : index;

		return <label
			key={color}
			htmlFor={`color-picker-swatch-${color}`}
			className="color-picker__swatch"
			ariaLabel={a11yColorLabel}
		>
			<input
				type="radio"
				name={`color-picker-swatches${isCustom ? '-custom' : ''}`}
				id={`color-picker-swatch-${color}`}
				className="color-picker__swatch__radio"
				value={color}
				on-change={() =>  {
					dispatch(INTERNAL_COLOR_SET, {
						value: color
					});
				}}
				checked={selectedColor === color}
				style={{'background-color': color}}
				hook-insert={({elm}) => setTargetRef(elm, refIndex)}
				hook-destroy={() => setTargetRef(null, refIndex)}
			/>
			{
				selectedColor === color ?
					<now-icon
						className={contrastClass}
						icon="check-outline"
						size="sm"
					/> : ''
			}
			{renderTooltip(state, colorLabel, refIndex)}
		</label>;
	}) : '';
  