import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import view from './view';
import styles from './_styles.scss';

const defaultColor = '#278EFC';

/**
 * Color picker swatch showcasing currently selected color.
 *
 * ```jsx
 *
 * <sn-color-picker-swatch selectedColorHex={hex} options={object} />
 * ```
 *
 * @seismicElement sn-color-picker-swatch
 * @summary Color picker swatch showcasing currently selected color.
 */
createCustomElement('sn-color-picker-swatch', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {
		targetRef: null
	},
	properties: {
		selectedColorHex: {default: defaultColor}
	},
	actionHandlers: {},
	dispatches: {}
});
