import buttonsToDisplay from './utils/generateButtonsConfig';

export const handleInitialProperties = ({
	updateState,
	state: {
		properties: {includeButtons, initialColorHex, selectedColorHex}
	}
}) => updateState({
	buttons: buttonsToDisplay({includeButtons, initialColorHex, selectedColorHex})
});

export const handlePropertyChanges = ({
	updateState,
	state,
	action: {
		payload: {name}
	}
}) => {
	if (name === 'includeButtons' || name === 'selectedColorHex')
		handleInitialProperties({updateState, state});
};
