import {t} from 'sn-translate';

import '@servicenow/now-icon';
import '@servicenow/now-accordion';
import '@servicenow/now-button';

import renderSwatches from '../utils/renderSwatches';
import {
	INTERNAL_COLOR_SET,
	SWITCH_TO_CUSTOM_COLOR_SELECTION_ACTION_NAME,
	DEFAULT_PALETTE_ACCORDION_ID,
	CUSTOM_COLORS_ACCORDION_ID
} from '../constants';

export const handleColorSelection = (color, dispatch) => {
	dispatch(INTERNAL_COLOR_SET, {
		value: color
	});
};

export const view = (
	state,
	{dispatch, updateState}
) => {
	const {
		locals: {targetRef},
		properties: {paletteColors, selectedColorHex, showCustomColors, customColors, expandedAccordion}
	} = state;

	const setTargetRef = (ref, index) => {
		targetRef[index] = ref;

		setTimeout(() => updateState({targetRef}));
	};

	const paletteSwatches = renderSwatches({
		colors: paletteColors,
		selectedColor: selectedColorHex,
		state,
		dispatch,
		setTargetRef,
		isCustom: false
	});

	return (
		<div id="cpPalettePanel" className="color-picker-swatches">
			{showCustomColors ?
				<now-accordion
					expandSingle={true}
					headingLevel="2"
					triggerIcon={{type: 'chevron', position: 'end'}}
					className="color-picker-swatches__accordion"
				>
					<now-accordion-item
						header={{label: t('Category Colors')}}
						expanded={expandedAccordion === DEFAULT_PALETTE_ACCORDION_ID}
						component-id={DEFAULT_PALETTE_ACCORDION_ID}
						append-to-payload={{view: 'swatches'}}
					>
						<div
							slot="content"
							className="color-picker-swatches__group-container"
						>
							{paletteSwatches}
						</div>
					</now-accordion-item>
					<now-accordion-item
						header={{label: t('Custom Colors')}}
						expanded={expandedAccordion === CUSTOM_COLORS_ACCORDION_ID}
						component-id={CUSTOM_COLORS_ACCORDION_ID}
						append-to-payload={{view: 'swatches'}}
					>
						<div
							slot="content"
							className="color-picker-swatches__group-container"
						>
							<now-button-iconic
								icon="plus-outline"
								tooltipContent={t('Add Custom Color')}
								size="md"
								variant="tertiary"
								configAria={{button:{'aria-label': t('Add Custom Color')}}}
								append-to-payload={{actionName: SWITCH_TO_CUSTOM_COLOR_SELECTION_ACTION_NAME}}
								className="color-picker__swatch color-picker-swatches__add-button"
							/>
							{
								renderSwatches({
									colors: customColors,
									selectedColor: selectedColorHex,
									state,
									dispatch,
									setTargetRef,
									indexOffset: paletteColors.length,
									isCustom: true
								})
							}
						</div>
					</now-accordion-item>
				</now-accordion>
				:
				<div className="color-picker-swatches__group-container">
					{paletteSwatches}
				</div>
			}
		</div>
	);
};
