import {t} from 'sn-translate';

import {INTERNAL_COLOR_SET} from '../../constants';

import {ARROW_KEYS} from '../constants';

import getA11yMessage from './getA11yMessage';

const stepAdjustment = 1;

export default ({event, x, y, h, a, dispatch, updateState}) => {
	const {key} = event;
	const {up, down, left, right} = ARROW_KEYS;

	let newX = x;
	let newY = y;

	if (Object.values(ARROW_KEYS).includes(key)) {
		event.preventDefault();

		switch (key) {
			case down:
				newY = Math.min(newY + stepAdjustment, 100);
				break;
			case up:
				newY = Math.max(newY - stepAdjustment, 0);
				break;
			case right:
				newX = Math.min(newX + stepAdjustment, 100);
				break;
			case left:
				newX = Math.max(newX - stepAdjustment, 0);
				break;
		}

		updateState({
			updateLensOnPropChange: false,
			lens: {x: newX, y: newY},
			a11yLiveMessage: getA11yMessage({x: newX, y: newY})
		});
		dispatch(INTERNAL_COLOR_SET, {
			value: {
				h,
				s: (newX/100).toFixed(2),
				v: (1 - newY/100).toFixed(2),
				a
			}
		});
	}
};
