
import '@servicenow/now-tabs';
import '@servicenow/now-button';

import '../sn-color-picker-swatches';
import '../sn-color-picker-list';
import '../sn-color-picker-palette';
import '../sn-color-picker-buttons';

import {SWATCHES_TAB_ID, LIST_TAB_ID, PALETTE_TAB_ID} from './constants';

const getSelectedView = ({
	selectedTab,
	hex,
	rgba,
	hsv,
	palette,
	showCustomColors,
	customColors,
	focusFirstPaletteElement,
	expandedSwatchesAccordion,
	expandedListAccordion
}) => {
	switch (selectedTab) {
		case SWATCHES_TAB_ID:
			return <sn-color-picker-swatches
				selectedColorHex={hex}
				paletteColors={palette}
				showCustomColors={showCustomColors}
				customColors={customColors}
				expandedAccordion={expandedSwatchesAccordion}
			/>;
		case LIST_TAB_ID:
			return <sn-color-picker-list
				selectedColorHex={hex}
				paletteColors={palette}
				showCustomColors={showCustomColors}
				customColors={customColors}
				expandedAccordion={expandedListAccordion}
			/>;
		case PALETTE_TAB_ID:
			return <sn-color-picker-palette
				selectedColorHex={hex}
				selectedColorRgba={rgba}
				selectedColorHsv={hsv}
				paletteColors={palette}
				showCustomColors={showCustomColors}
				customColors={customColors}
				focusFirstElement={focusFirstPaletteElement}
			/>;
		default:
			return '';
	}
};

export default state => {
	const {
		tabs,
		buttons,
		selectedTab,
		selectedColor: {
			hex,
			rgba,
			hsv
		},
		initialColor,
		validatedPalette,
		stateCustomColors,
		focusFirstPaletteElement,
		expandedSwatchesAccordion,
		expandedListAccordion,
		properties: {showBorders, showCustomColors}
	} = state;

	return (
		<div class={{
			'color-picker': true,
			'color-picker--with-borders': showBorders
		}}>

			{/* COLOR PICKER TABS */}
			{tabs.length > 1 ?
				<now-tabs items={tabs} fixedWidth={true} selectedItem={selectedTab} hide-label className='color-picker__tabs' />
				: ''
			}

			{/* COLOR PICKER VIEW */}
			<div class={{
				'color-picker__tab-container': true,
				'color-picker__tab-container--vertical-padding': showBorders && tabs.length <= 1,
				'color-picker__tab-container--no-top-padding': showCustomColors && selectedTab !== PALETTE_TAB_ID
			}}>
				{getSelectedView({
					selectedTab,
					hex,
					rgba,
					hsv,
					palette: validatedPalette,
					showCustomColors,
					customColors: stateCustomColors,
					focusFirstPaletteElement,
					expandedSwatchesAccordion,
					expandedListAccordion
				})}
			</div>

			{/* COLOR PICKER BUTTONS */}
			{buttons.length > 0 && tabs.length > 0 ?
				<sn-color-picker-buttons includeButtons={buttons} selectedColorHex={hex} initialColorHex={initialColor} />
				: ''
			}

		</div>
	);
};
