
/* istanbul ignore file */
import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import debounce from 'lodash.debounce';

import Color from '../color';
import {VALID_BUTTONS} from '../sn-color-picker-buttons/constants';
import {
	DEFAULT_PALETTE,
	DEFAULT_COLOR,
	INTERNAL_COLOR_SET,
	INTERNAL_FOCUS_SET,
	COLOR_SCHEMA,
	DEFAULT_PALETTE_ACCORDION_ID
} from '../constants';

import {VALID_TABS} from './constants';
import {
	handleInitialProperties,
	handlePropertyChanges,
	handleTabSelection,
	handleColorChange,
	handleCustomColor,
	handleAccordionItemExpansion
} from './effects';
import view from './view';
import styles from './_styles.scss';

const defaultColor = new Color();
defaultColor.setColor(DEFAULT_COLOR);

/**
* A component which allows selection of color using any combination of three views:
* - **swatches** -  a grid of predefined colors displayed as swatches;
* - **list** - same as swatches but displayed as a list;
* - **palette** - HSV selection area (with hue slider and saturation-value picker), RGBA input fields and HEX input field;
*
* ```jsx
*
* <now-color-picker
*		includeTabs={['swatches', 'list', 'palette']}
* 	includeButtons={['save', 'cancel']}
*		palette={[
* 		{
* 			value: '#FFFFFF',
* 			displayValue: 'White'
* 		}
*		]}
*		selectedColor="#FFFFFF"
* 	showBorders={true}
* />
* ```
*
* @seismicElement now-color-picker
* @summary A component which allows selection of color.
*/

/**
 * @type {Color}
 * @typedef {Object} Color
 * @description A representation of color value and associated optional alias.
 * @property {string} value - HEX value representing color. 8 digit HEXes are supported.
 * @property {string} displayValue - Optional descriptive alias for the color.
 */
createCustomElement('now-color-picker', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		/**
		 * Array of tab names to be shown. Tabs will be shown in the order they are declared inside the array.
		 * 
		 * @type {(Array.<("swatches" | "list" | "palette")>)}
		 * @uib.description Array of tab names to be shown. Tabs will be shown in the order they are declared inside the array.
		 */
		includeTabs: {
			default: ['swatches', 'list', 'palette'],
			required: true,
			schema: {
				type: 'array',
				items: {
					type: 'string',
					enum: VALID_TABS
				}
			}
		},
		/**
		 * Array of buttons to be shown. `Cancel` button is **always** going to be shown before `Save`.
		 * 
		 * @type {(Array.<("save" | "cancel")>)}
		 * @uib.description Array of buttons to be shown. Cancel button is always going to be shown before Save.
		 */
		includeButtons: {
			default: ['cancel', 'save'],
			required: false,
			schema: {
				type: 'array',
				items: {
					type: 'string',
					enum: VALID_BUTTONS
				}
			}
		},
		/**
		 * Array of color objects to be shown in the **swatches** and **list** views.
		 * 
		 * @type {(Array.<Color>)}
		 * @uib.description Array of color objects to be shown in the swatches and list views.
		 */
		palette: {
			default: DEFAULT_PALETTE,
			required: false,
			schema: {
				type: 'array',
				items: COLOR_SCHEMA
			}
		},
		/**
		 * HEX value of selected color. 8 digit HEXes are supported.
		 * 
		 * @type {string}
		 * @uib.description HEX value of selected color. 8 digit HEXes are supported.
		 */
		selectedColor: {
			default: defaultColor,
			required: false,
			schema: {
				type: 'string'
			}
		},
		/**
		 * When true, the color picker appears with borders on all sides.
		 * @type {boolean}
		 * @uib.description Indicates whether the color picker appears with borders on all sides or not.
		 */
		showBorders: {
			default: false,
			schema: {type: 'boolean'}
		},
		/**
		 * When true, custom colors functionality is enabled.
		 * @type {boolean}
		 * @uib.description When true, custom colors functionality is enabled.
		 */
		showCustomColors: {
			default: true,
			required: false,
			schema: {
				type: 'boolean'
			}
		},
		/**
		 * Array of color objects to be shown in the custom colors section.
		 * 
		 * @type {(Array.<Color>)}
		 * @uib.description Array of color objects to be shown in the custom colors section.
		 */
		customColors: {
			default: [],
			required: false,
			schema: {
				type: 'array',
				items: COLOR_SCHEMA
			}
		}
	},
	initialState: {
		tabs: [],
		selectedTab: '',
		validatedColors: [],
		selectedColor: {
			hsv: defaultColor.getHSV(),
			rgba: defaultColor.getRGB(),
			hex: DEFAULT_COLOR,
		},
		buttons: [],
		initialColor: null,
		validatedPalette: [],
		stateCustomColors : [],
		focusFirstPaletteElement: false,
		expandedSwatchesAccordion: DEFAULT_PALETTE_ACCORDION_ID,
		expandedListAccordion: DEFAULT_PALETTE_ACCORDION_ID
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: handleInitialProperties,
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: handlePropertyChanges,
		'NOW_TABS#SELECTED_ITEM_SET': handleTabSelection,
		[INTERNAL_COLOR_SET]: debounce(handleColorChange),
		[INTERNAL_FOCUS_SET]: ({updateState}) => updateState({focusFirstPaletteElement: false}),
		'NOW_BUTTON_ICONIC#CLICKED': handleCustomColor,
		'NOW_BUTTON_BARE#CLICKED': handleCustomColor,
		'NOW_ACCORDION_ITEM#EXPANDED_SET': handleAccordionItemExpansion
	},
	dispatches: {
		/**
		 * Dispatched when a new selection is made in any of the color picker views. Payload contains selected color string in HEX format, e.g. `#278EFC00`.
		 *
		 * @type {{value: string}}
		 * @uib.description Dispatched when a new selection is made in any of the color picker views. Payload contains selected color string in HEX format, e.g. #278EFC00.
		 */
		'NOW_COLOR_PICKER#SELECTED_COLOR_SET': {},
		/**
		 * Dispatched when `Save` button is clicked. Payload contains selected color string is in HEX format.
		 *
		 * @type {{value: string}}
		 * @uib.description Dispatched when Save button is clicked. Payload contains selected color string is in HEX format.
		 */
		'NOW_COLOR_PICKER#SAVE_CLICKED': {},
		/**
		 * Dispatched when `Cancel` button is clicked. Payload contains initial color string is in HEX format.
		 *
		 * @type {{value: string}}
		 * @uib.description Dispatched when Cancel button is clicked. Payload contains initial color string is in HEX format.
		 */
		'NOW_COLOR_PICKER#CANCEL_CLICKED': {},
		/**
		 * Dispatched when a new custom color is added.
		 *
		 * @type {{value: string}}
		 * @uib.description Dispatched when a new custom color is added.
		 */
		'NOW_COLOR_PICKER#CUSTOM_COLOR_ADDED': {},
		/**
		 * Dispatched when a custom color is deleted.
		 *
		 * @type {{value: string}}
		 * @uib.description Dispatched when a custom color is deleted.
		 */
		'NOW_COLOR_PICKER#CUSTOM_COLOR_DELETED': {}
	}
});
